@use "./mixins/disable-mob-hover" as *;
.social {
  &__list {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
  &__link {
    display: flex;
    height: 2rem;
    width: 2rem;
    @include hover{
      &:hover{
        .social__icon{
          fill: var(--white);
        }
      }
    }
  }
  &__icon {
    height: 100%;
    width: 100%;
    fill: var(--light-grey);
    transition: .3s;
    
  }
}
